* {
  box-sizing: border-box;
  font-size: 17px;
  font-weight: 500;
  font-family: 'Times New Roman', Times, serif;
}

.App {
  margin: 0;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.patient {
  background-color: whitesmoke;
  margin-bottom: 2px;
  width: 100%;
}

.tasks {
  background-color: whitesmoke;
  width: 400px;
  border: 3px solid rgb(47, 65, 183);
  padding: 5px;
  margin: 20px;
}
.App-nav {
  height: auto;
  display: flex;
  flex-direction: row;
  background-color: white;
  padding: 5px 10px;
  box-shadow: 2px 2px 2px 1px rgb(164, 176, 243);
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 1%;
}

.App-logo {
  max-width: 100px;
  
}

input {
  width: 250px; 
  height: 35px; 
  border: 3px solid rgb(47, 65, 183);
}

button{
  background-color: white;
  text-decoration: none;
  padding: 7px 25px;
  border: 1px solid rgb(47, 65, 183);
  cursor: pointer;
}

select {
  background-color: white;
  text-decoration: none;
  padding: 7px 25px;
  border: 1px solid rgb(47, 65, 183);
  cursor: pointer;
}

.Tasks {
  border-radius: 5px;
  background-color: white;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
}

table {
  border: 2px solid rgb(238, 239, 244);
  width: 100%;
  height: auto;
}

th {
  border-bottom: 1px solid rgb(246, 246, 248);
  width: auto;
}

td {
  text-align: center;
}

.dashboard {
  display: flex;
  flex-direction: row;
  background-color: white;
  padding: 10px;
  justify-content: space-evenly;
  width: 100%;
}

.landing {
  margin-top: 10%;
  margin-left: 40%;
  align-items: center;
  justify-content: center;
}

.google {
  margin-top: 1.5%;
  margin-left: 37%;
}

.newForm {
  margin-left: 10%;
  margin-top: 2%;
  margin-bottom: 3%;
}

.customers {
  border-collapse: collapse;
  width: 100%;
}

.customers td, .customers th {
  border: 1px solid #ddd;
  padding: 8px;
}

.customers tr:nth-child(even){background-color: #f2f2f2;}

.customers tr:hover {background-color: #ddd;}

.customers th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #172369;
  color: white;
}

h4 {
  font-weight: bolder;
  text-align: center;
}
